import {useSelector} from "react-redux";
import Folder from "./Folder";
import add from "../../img/logos/add.svg";
import {useState} from "react";
import NewNote from "../Notes/NewNote";
import Modal from "../Common/Modal";
import NewFolder from "./NewFolderForm";

const Folders = (props) => {
    const existingFolders = useSelector(state => state.folders.foldersList.allFolders);

    const [isModal, setModal]= useState(false)

    return (
        <div className={"folders_container"}>
            <h2>Notes Folders</h2>
            {existingFolders.map(e => <Folder key={e.id} id={e.id} name={e.name} color={e.color} />)}

            <button title={"Add New Folder"} className={"app_button"} onClick={() => setModal(true)}>
                <img className={"button_logo"} src={add} alt={"add"}/>
            </button>

            <Modal
                isVisible={isModal}
                title="New Folder"
                content={<NewFolder/>}
                /*footer={<button>Cancel</button>}*/
                onClose={() => setModal(false)} />

        </div>
    )
}

export default Folders