import Folders from "../Folders/Folders";
import NewFolder from "../Folders/NewFolderForm";
import FolderNotes from "../Folders/FolderNotes";
import BurgerFolders from "../Folders/BurgerFolders";

const MainContent = (props) => {

    return (
        <div className={"main_container"}>
            <div className={"desktop_folders"}>
                <Folders/>
                {/*<NewFolder/>*/}
            </div>
            <div className={"tablet_folders"}>
                <BurgerFolders/>
            </div>

            <div className={"notes_block"}>
                <FolderNotes/>
            </div>
        </div>
    )
}

export default MainContent